        /************************************** WHOLE PAGE CSS **************************/

        *{
          margin: 0;
          padding: 0;
          font-family: 'Poppins', sans-serif;
          box-sizing: border-box;
        }
        html{
          scroll-behavior: smooth;
        }
        body{
          background:black;
          color: #fff;
        }
        
        
        
        
        
        
                /********************************************* HEADER CSS ****************************************/
        
        #header{
          width: 100%;
          height: 100vh;
          /* background-size: cover; */
          background-position: center;
          background-repeat: no-repeat;
        
          background-image: url('./photo.png');
        }
        
        #header img{
          height: 200px;
          width: 300px;
        }
        
        .con{
          padding: 10px 10%;
        }
        
        nav{
          display: flex;
          align-items: center;
          justify-content: space-between;
          flex-wrap: wrap;
        }
        
        .logo{
          width: 140px;
        }
        
        nav ul li{
          display: inline-block;
          list-style: none;
          margin: 10px 20px;
        }
        
        nav ul li a{
          color:  #fff;
          text-decoration: none;
          font-size: 18px;
          position: relative;
        }
        
        nav ul li a::after{
          content: '';
          width: 0;
          height: 3px;
          background: #ff004f;
          position: absolute;
          left: 0;
          bottom: -6px;
          transition: 0.5s;
        }
        
        nav ul li a:hover::after{
          width: 100%;
        }
        
        .header-text{
          margin-top: 10%;
          font-size: 30px;
        }
        
        .header-text h1{
          font-size: 60px;
          margin-top: 20px;
        }
        
        .header-text h1 span{
          color: #ff004f;
        }
        
        nav{
          background-color: black;
          padding-top: 0%;
          margin-top: 0%;
        }
        
        .navbar-brand{
          font-size: 30px;
        }
        
        .navbar-brand span{
          color: #ff004f;
        }
        
        
        
        
        
              /********************************** ABOUT CSS *********************************************************/
        
        #about{
          padding: 80px 0;
          color: #ababaa;
        }
        .ro{
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
        }
        
        .about-col-1{
          flex-basis: 35%;
        }
        .about-col-1 img{
          width: 100%;
          border-radius: 15px;
        }
        
        .about-col-2{
          flex-basis: 60%;
        }
        
        .sub-title{
          font-size: 60px;
          font-weight: 600;
          color: #fff;
        }
        
        .tab-titles{
          display: flex;
          margin: 20px 0 40px;
        }
        
        .tab-links{
          margin-right: 50px;
          font-size: 18px;
          font-weight: 500;
          cursor: pointer;
          position: relative;
        }
        
        .tab-links::after{
          content: '';
          width: 0;
          height: 3px;
          background: #ff004f;
          position: absolute;
          left: 0;
          bottom: -8px;
          transition: 0.5s;
        }
        
        .tab-links.active-link::after{
          width: 50%;
        }
        
        .tab-contents ul li{
          list-style: none;
          margin: 10px 0;
        }
        
        .tab-contents ul li span{
          color: #b54769;
          font-size: 14px;
        }
        
        
        .tab-contents{
          display: none;
        }
        
        .tab-contents.active-tab{
          display: block;
        }
        
        
        
        
        
        /******************************************* SERVICES CSS *******************************************/
        
        .services{
          padding: 30px 0;
        }
        
        .services-list{
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
          grid-gap: 40px;
          margin-top: 50px;
        }
        
        .services-list div{
          background: #262626;
          padding: 40px;
          font-size: 13px;
          font-weight: 300;
          border-radius: 10px;
          transition: background 0.5s, transform 0.5s;
        }
        
        .services-list div i{
          font-size: 30px;
          margin-bottom: 30px;
        }
        
        .services-list div h2{
          font-size: 30px;
          font-weight: 500;
          margin-bottom: 15px;
        }
        
        .services-list div:hover{
          background: #ff004f;
          transform: translateY(-10px);
        }
        
        
        
        /**************************** PORTFOLIO CSS **********************************/
        
        #portfolio{
          padding: 50px 0;
        }
        
        .work-list{
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
          grid-gap: 40px;
          margin-top: 50px;
        }
        
        .work{
          border-radius: 10px;
          position: relative;
          overflow: hidden;
        }
        
        .work img{
          width: 100%;
          height: 400px;
          border-radius: 10px;
          display: block;
          transition:  transform 0.5s;
        }
        
        .layer{
          width: 100%;
          height: 0;
          background: linear-gradient(rgba(0,0,0,0.6), #ff004f);
          border-radius: 10px;
          position: absolute;
          left: 0;
          bottom: 0;
          overflow: hidden;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          padding: 0 40px;
          text-align: center;
          font-size: 14px;
          transition: height 0.5s;
        }
        
        .layer h3{
          font-weight: 500;
          margin-bottom: 20px;
        }
        
        .layer a{
          margin-top: 20px;
          color: #ff004f;
          text-decoration: none;
          font-size: 18px;
          line-height: 60px;
          background: #fff;
          width: 60px;
          height: 60px;
          border-radius: 50%;
          text-align: center;
        }
        
        .work:hover img{
          transform: scale(1.1);
        }
        
        .work:hover .layer{
          height: 100%;
        }
        
        
        
        
        
        
        /**************************** CONTACT CSS ***********************/
        
        .contact-left{
          flex-basis: 35%;
        }
        
        .contact-right{
          flex-basis: 60%;
        }
        
        .contact-left p{
          margin-top: 30px;
        }
        
        .contact-left p i{
          color: #ff004f;
          margin-right: 15px;
          font-size: 25px;
        }
        
        .social-icons a:hover{
          color: #ababaa;
          transform: translateY(-5px);
        }
        
        .btn.btn2{
          display: inline-block;
          background: #ff004f;
          color: white;
        }
        
        .contact-right form{
          width: 100%;
        }
        
        form input, form textarea{
          width: 100%;
          border: 0;
          outline: none;
          background: #262626;
          padding: 15px;
          margin: 15px 0;
          color: #fff;
          font-size: 18px;
          border-radius: 6px;
        }
        
        #contact .container{
          margin-top: 80px;
          margin-bottom: 80px;
        }
        
        .copyright{
          padding-top: 50px;
          width: 100%;
          text-align: center;
          padding: 25px 0;
          background: #262626;
          font-weight: 300;
          margin-top: 20px;
        }
        
        .copyright i{
          color: #ff004f;
        }
        
        
        
        /************************  FOR SMALL SCREEN ***********************/
        
        @media only screen and (max-width:600px){
          .sub-title{
            font-size: 40px;
          }
          .about-col-1, .about-col-2{
            flex-basis: 100%;
          }
          .about-col-1{
            margin-bottom: 30px;
          }
          .about-col-2{
            font-size: 14px;
          }
          .tab-links{
            font-size: 16px;
            margin-right: 20px;
          }
          .contact-left, .contact-right{
            flex-basis: 100%;
          }
          .copyright{
            font-size: 14px;
            width: 100%;
          }
        
          #header{
            background-image:none;
          }
        }
        
        #message{
          color: #61b752;
          margin-top: 40px;
          display: block;
        }